  display: inline-flex;
  align-items: center;
  flex-shrink: 0;
  width: 2.25rem;
  height: 1.25rem;
  min-width: fit-content;
  caret-color: transparent; }

[data-switch] [data-switch-slide] {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 0.75rem;
  background-color: var(--colol-gull-gray--50, rgba(152, 162, 179, 0.5));
  transition: all 0.15s ease-in-out; }

[data-switch]:hover [data-switch-slide] {
